<template>
  <div id="portal" >
  
    <!--<div id="zhuti" class="container">
      <h1>区块链是什么？</h1>
    </div>-->
  <el-carousel indicator-position="outside"  height="500px" style="margin:0px; background-color:black;">
    <el-carousel-item v-for="img in imgs" :key="img">
      <img :src="img" style="width:60%;height:100%">
    </el-carousel-item>
  </el-carousel>
  <div id="jieshao" class="col-xs-12 col-sm-8" >
      <span class="hidden-xs">
        <span style="vertical-align: middle"><div class="container"><strong>2017 www.webank.com|微众银行WeBee团队</strong></div></span>
      </span> 
  </div>  
  </div>
</template>

<script>

import Vue from 'vue'
import img1 from '../assets/futrue.jpg'
import img2 from '../assets/blockchain.jpg'
import img3 from '../assets/webee.jpg'

export default {
  name: 'portal',
  data() {
    return {
      imgs:[
        img2,
        img3,
        img1
      ]
    }
  },
  
}


</script>

<style>
#portal {
  width: 100%;
  height: 100%;
  background-color: black;
  font-family: "Microsoft YaHei";  
}

#jieshao {
  width: 100%;
  height: 10%;
  background-color: black;
}

.portaltitle {
  font-size: 30px;
}

.el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
.el-carousel__item:nth-child(2n) {
    background-color: black;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: black; 
}

</style>



