<template>
    <div class="modal-mask">
      <div class="modal-container">
        <div class="modal-header">
          <slot name="header"></slot>
        </div>
        <div class="modal-body">
          <slot></slot>
        </div>
      </div>
    </div>
</template>
<script>
export default {
    name: 'modal',
}
</script>
<style>
.modal-mask {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-container {
  z-index: 3;
  padding:20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
}

.modal-header {
    height: 40px;
    text-align: center;
    font-family: 'Microsoft YaHei';
    font-size: 20px;
    padding:0px;
}
</style>


