              <el-button type="success" v-bind:disabled="p4==1?true:false" @click="invokeProcess('ip4')">Invoke
              </el-button>
              <el-button type="danger" v-bind:disabled="p4==0?true:false" @click="killProcess('ip4')">Kill</el-button>
            </el-col>
            <el-col :span="8">
