              <el-button type="success" v-bind:disabled="p7==1?true:false" @click="invokeProcess('ip7')">Invoke
              </el-button>
              <el-button type="danger" v-bind:disabled="p7==0?true:false" @click="killProcess('ip7')">Kill</el-button>
            </el-col>
