  </div>


</template>

<script>

  import Vue from 'vue';

  import $ from 'jquery';

  var baseUrl = 'http://119.29.181.234:8089';

  export default {
    name: 'process',
    data() {
      return {
        intervalId: null,
        p1: 0,
        p2: 0,
        p3: 0,
        p4: 0,
        p5: 0,
        p6: 0,
        p7: 0
      }
    },
    methods: {
      getAllProcessStatus: function () {
        var ctx = this;
        $.ajax({
          type: "post",
          url: baseUrl + '/isnormal',
          cache: "false",
          data: JSON.stringify([{}]),
          dataType: "json",
          contentType: 'application/json',
          success: function (result) {
            result.forEach(function (p) {
              ctx['p1'] = "ip1" in p ? p['ip1'] : ctx['ip1'];
              ctx['p2'] = "ip2" in p ? p['ip2'] : ctx['ip2'];
              ctx['p3'] = "ip3" in p ? p['ip3'] : ctx['ip3'];
              ctx['p4'] = "ip4" in p ? p['ip4'] : ctx['ip4'];
              ctx['p5'] = "ip5" in p ? p['ip5'] : ctx['ip5'];
              ctx['p6'] = "ip6" in p ? p['ip6'] : ctx['ip6'];
              ctx['p7'] = "ip7" in p ? p['ip7'] : ctx['ip7'];
            })
          }
        });
      },
      killProcess: function (pid) {
        var ctx = this;
        this.$confirm('挂起进程, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          $.ajax({
            type: "post",
            url: baseUrl + '/killpid',
            cache: "false",
            data: JSON.stringify([{ip: pid}]),
            dataType: "json",
            contentType: 'application/json',
            success: function (result) {
              ctx.getAllProcessStatus();
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
      },
      invokeProcess: function (pid) {
        var ctx = this;
        this.$confirm('启动进程, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          $.ajax({
            type: "post",
            url: baseUrl + '/startpid',
            cache: "false",
            data: JSON.stringify([{ip: pid}]),
            dataType: "json",
            contentType: 'application/json',
            success: function (result) {
              ctx.getAllProcessStatus();
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });
        });
      }
      ,
      doTask: function () {
        console.log('轮询所有进程状态');
      }
    }
    ,
    created: function () {
      this.getAllProcessStatus();
      //两秒轮询一次所有进程的状态
      //this.intervalId = setInterval(this.doTask, 2000);
    }
    ,
    beforeDestroy: function () {
      //Vue路由跳转的时删除定时器
      clearInterval(this.intervalId);
    }
    ,
  }

</script>

<style>
  #portal {
    width: 100%;
    height: 100%;
    font-family: "Microsoft YaHei";
    /*color: white;*/
    font-weight: bold;
    background: url("../assets/a.png");

    /*background-color: #2c3e50;*/
  }

  .server-box {
    height: 500px;
    border-radius: 25px;
    border: 4px solid gray;
  }

  .process-box {
    height: 80%;
    border-bottom: 4px solid gray;
  }

  .process {
    margin-top: 30px;
  }

  .server {
    height: 20%;
  }
</style>



