<template>
  <div id="app">
    <div id="menu">
      <el-menu mode="horizontal" router  theme="dark" @select="handleSelect" v-bind:class="{ menu_color: is_black }">
        <div id="toububiaoti">
          <img style="width:262px;height:30px;float:left;" src="./assets/webanklogo.png" />
        </div>
        <!--<div id="toubumiddle">-->
        <el-menu-item index="portal">首页</el-menu-item>
        <el-menu-item index="handle">客户操作</el-menu-item>
        <el-menu-item index="process">进程管理</el-menu-item>
        <el-menu-item index="blockchain">区块链展示</el-menu-item>
        <el-menu-item index="credit">我的信用</el-menu-item>
        <el-menu-item index="about">关于</el-menu-item>
      </el-menu>
    </div>
    <div id="middle">
      <router-view></router-view>
    </div>
    <!--<div id="footer">
      <img style="width:70px;margin:0 auto" src="./assets/bee.png" />
    </div>-->
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
      return {
        is_black: false
      };
  },
  created() {
    this.handleSelect()
  },
  methods: {
      handleSelect(key, keyPath) {
        console.log(keyPath);
        if (keyPath == "portal") {
          this.is_black = true
        } else {
          this.is_black = false
        }
      }
  }
}
</script>

<style>
#toububiaoti {
  color: white;
  float: left;
  padding: 13px;
  height: 30px;
  text-align: left;
  width: 40%;
  min-width: 200px;
  font-size: 150%;
  margin-left: 50px;
  margin-right: 50px;
}


#app {
  font-family: "Microsoft YaHei";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width:100%;
  height: 100%;
  min-width: 1000px;
}

#footer {
  height: 10%;
  width: 100%;
}

#middle {
  width: 100%;
  height: 80%;  
}

body {
  font-family: "Microsoft YaHei";
  margin: 0px;
  width: 100%;
  height: 100%;
}

html {
  height: 100%
}


.v-menubar {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  color: blue;
}

.v-menu {

  width: 50px;
  font-size: 12px;
  text-align: center;
  line-height: 28px;
  position: relative;
  height: 10%;
}

.v-menu:hover {
  cursor: pointer;
  color: red
}


.opened {
  display: block
}

.zhuxiao {
  height: 25px;
  line-height: 25px;
  width: 70px;
  float: left;
}

.menu_color {
  background-color: black;
}
</style>



