<template>
  <div id="credit">
    <div id="top">
      <strong>我的信用分：</strong><strong>{{balance}}</strong>
    </div>
    <div id="mdl">
      <el-table :data="rows" stripe border style="width: 100%; font-size:10px">
        <el-table-column prop="id" label="流水号" align="left">
        </el-table-column>
        <el-table-column prop="num" label="操作" align="left">
        </el-table-column>
      </el-table>
    </div>
    <div id="btm">
      <el-pagination :current-page.sync="currentPage" layout="prev, pager, next, jumper" :page-count="pageCount"
                     @current-change="display(currentPage)">
      </el-pagination>
    </div>
  </div>
</template>

<script>

  import $ from 'jquery';
  var baseUrl = 'http://119.29.181.234:8089';

  export default {
    name: 'credit',
    data() {
      return {
        rows: [],
        currentPage: 1,
        pageCount: 1,
        pageSize: 10,
        balance: 0
      }
    },
    created() {
      this.display(1);
    },
    methods: {
      display: function (currentPage) {
        var ctx = this;
        $.ajax({
          type: "post",
          url: baseUrl + '/getpidcount',
          cache: "false",
          data: JSON.stringify([{ip: "ip1", page: currentPage}]),
          dataType: "json",
          contentType: 'application/json',
          success: function (result) {
            console.log(result);
            ctx.pageCount = result.total_page;
            ctx.rows = result.count_data;
            ctx.balance = result.balance;
          }
        });
      }
    }
  }

</script>

<style>
  #credit {
    width: 100%;
    height: 100%;
    font-family: "Microsoft YaHei";
    background: url("../assets/a.png");
  }

  #mdl {
    width: 70%;
    margin: 10px auto;
    min-width: 800px;
    height: auto;
  }

  #btm {
    width: 80%;
    margin: 20px auto;
    font-size: 14px;
    min-width: 800px;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    text-align: left;
    margin: 0px;
    width: 90%;
    float: left;
    font-size: 10px;
  }

  .demo-table-expand .el-form-item span {
    font-size: 10px;
  }
</style>



