// 这里面负责写路由映射，便于管理

// 引入路由模块并使用它
import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

// 创建路由实例并配置路由映射  
// path:'*',redirect:'/home'  重定向到path是/home的映射
const router = new VueRouter({
  routes:[
  {
      name:'portal', path: '/portal', component: require('../views/portal.vue')
  },
  {
      name:'handle', path: '/handle', component: require('../views/handle.vue')
  },
  {
      name:'process', path: '/process', component: require('../views/process.vue')
  },
  {
      name:'blockchain', path: '/blockchain', component: require('../views/blockchain.vue')
  },
  {
      name: 'credit', path: '/credit', component: require('../views/credit.vue')
  },
  {
      name:'about', path: '/about', component: require('../views/about.vue')
  },
  {   path:'/', redirect:'/portal'
  }]
})


// 输出router
export default router;


