import Vue from 'vue'
import axios from 'axios'
import * as apis from './apis'

import $ from 'jquery'

var baseUrl = 'http://119.29.181.234:8089'

//提交客户数据
export function submit_data(cb, submitData) {
    console.log("post接口" + apis.SUBMIT_INPUT_DATA + "的提交信息:")
    console.log(submitData)
    $.ajax({
        type: "post",
        url: baseUrl + apis.SUBMIT_INPUT_DATA,
        cache: "false",
        data: JSON.stringify(submitData),
        dataType: "json",
        contentType: 'application/json',
        success: function (result) {
            console.log(result);
            cb(result)
        }
    });

}

//获取区块链详细数据
