<template>
    <div id="blockchain">
        <div class="bc_first">
            <el-tabs v-model="active_tab" @tab-click="tab_click()">
                <el-tab-pane label="进程1" name="ip1"></el-tab-pane>
                <el-tab-pane label="进程2" name="ip2"></el-tab-pane>
                <el-tab-pane label="进程3" name="ip3"></el-tab-pane>
                <el-tab-pane label="进程4" name="ip4"></el-tab-pane>
                <el-tab-pane label="进程5" name="ip5"></el-tab-pane>
                <el-tab-pane label="进程6" name="ip6"></el-tab-pane>
                <el-tab-pane label="进程7" name="ip7"></el-tab-pane>
            </el-tabs>
        </div>
        <div class="bc_second">
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="block" v-bind:class="{ 'block_yellow': bc_table_1.is_yellow, 'block_blue': bc_table_1.is_blue, 'block_red': bc_table_1.is_red,  }"></div>
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="line_connect">
                <hr v-bind:class="{ line: bc_table_2.is_blue }" />
            </div>
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="block" v-bind:class="{ 'block_yellow': bc_table_2.is_yellow, 'block_blue': bc_table_2.is_blue, 'block_red': bc_table_2.is_red,  }"></div>
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="line_connect">
                <hr v-bind:class="{ line: bc_table_3.is_blue }" />
            </div>
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="block" v-bind:class="{ 'block_yellow': !bc_table_3.is_blue, 'block_blue': bc_table_3.is_blue, 'block_red': bc_table_3.is_blue,  }"></div>
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="line_connect">
                <hr v-bind:class="{ line: bc_table_4.is_blue }" />
            </div>
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="block" v-bind:class="{ 'block_yellow': !bc_table_4.is_blue, 'block_blue': bc_table_4.is_blue, 'block_red': bc_table_4.is_blue,  }"></div>
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="line_connect">
                <hr v-bind:class="{ line: bc_table_5.is_blue }" />
            </div>
    
            <div class="line_div">
                <hr class="line" />
            </div>
            <div class="block" v-bind:class="{ 'block_yellow': !bc_table_5.is_blue, 'block_blue': bc_table_5.is_blue, 'block_red': bc_table_5.is_blue,  }"></div>
            <div class="line_div">
                <hr class="line" />
            </div>
        </div>
        
        <div class="bc_third">
            <div style="width:100%;">
                <div class="hash">{{this.bc_table_1.hash}}</div>
                <div class="hash">{{this.bc_table_2.hash}}</div>
                <div class="hash">{{this.bc_table_3.hash}}</div>
                <div class="hash">{{this.bc_table_4.hash}}</div>
                <div class="hash">{{this.bc_table_5.hash}}</div>
            </div>
            <div class="bc_first1">
                <el-table class="bc_table" :data="bc_table_1.data" stripe border style="width: 100%">
                    <el-table-column prop="id" label="单号" header-align="left" show-overflow-tooltip align="left">
                    </el-table-column>
                    <el-table-column prop="num" label="分数" header-align="left" show-overflow-tooltip align="left" width="60">
                    </el-table-column>
                    <el-table-column label="" width="50" style="padding:0px;background-color:red;">
                        <template scope="scope">
                            <el-button @click.native.prevent="edit(scope.$index, bc_table_1.data)" size="small" type="text">
                                编辑
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="bc_first2"></div>
            <div class="bc_first1">
                <el-table class="bc_table" :data="bc_table_2.data" stripe border style="width: 100%">
                    <el-table-column prop="id" label="单号" header-align="left" show-overflow-tooltip align="left">
                    </el-table-column>
                    <el-table-column prop="num" label="分数" header-align="left" show-overflow-tooltip align="left" width="60">
                    </el-table-column>
                    <el-table-column label="" width="50" style="padding:0px;background-color:red;">
                        <template scope="scope">
                            <el-button @click.native.prevent="edit(scope.$index, bc_table_2.data)" size="small" type="text">
                                编辑
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="bc_first2"></div>
    
            <div class="bc_first1">
                <el-table class="bc_table" :data="bc_table_3.data" stripe border style="width: 100%">
                    <el-table-column prop="id" label="单号" header-align="left" show-overflow-tooltip align="left">
                    </el-table-column>
                    <el-table-column prop="num" label="分数" header-align="left" show-overflow-tooltip align="left" width="60">
                    </el-table-column>
                    <el-table-column label="" width="50" style="padding:0px;background-color:red;">
                        <template scope="scope">
                            <el-button @click.native.prevent="edit(scope.$index, bc_table_3.data)" size="small" type="text">
                                编辑
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="bc_first2"></div>
    
            <div class="bc_first1">
                <el-table class="bc_table" :data="bc_table_4.data" stripe border style="width: 100%">
                    <el-table-column prop="id" label="单号" header-align="left" show-overflow-tooltip align="left">
                    </el-table-column>
                    <el-table-column prop="num" label="分数" header-align="left" show-overflow-tooltip align="left" width="60">
                    </el-table-column>
                    <el-table-column label="" width="50" style="padding:0px;background-color:red;">
                        <template scope="scope">
                            <el-button @click.native.prevent="edit(scope.$index, bc_table_4.data)" size="small" type="text">
                                编辑
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="bc_first2"></div>
    
            <div class="bc_first1">
                <el-table class="bc_table" :data="bc_table_5.data" stripe border style="width: 100%">
                    <el-table-column prop="id" label="单号" header-align="left" show-overflow-tooltip align="left">
                    </el-table-column>
                    <el-table-column prop="num" label="分数" header-align="left" show-overflow-tooltip align="left" width="60">
                    </el-table-column>
                    <el-table-column label="" width="50" style="padding:0px;background-color:red;">
                        <template scope="scope">
                            <el-button @click.native.prevent="edit(scope.$index, bc_table_5.data)" size="small" type="text">
                                编辑
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            
            
            <div style="clear:both;"></div>
            <modal v-if="bc_edit_visible">
                <template slot="header">编辑
                    <div style="float:right"><i @click="bc_edit_visible=false" class="el-icon-circle-cross"></i></div>
                </template>
                <el-form :inline="true" :model="bc_edit_form" label-position="left" ref="ruleForm" label-width="70px">
                    <div class="bc_formitem">
                        <el-form-item label="流水单号" prop="id">
                            <el-input :disabled="true" v-model="bc_edit_form.id" placeholder="流水单号" class="bc_formiteminput" size="small"></el-input>
                        </el-form-item>
                    </div>
                    <div class="bc_formitem">
                        <el-form-item label="分数" prop="num">
                            <el-input v-model="bc_edit_form.num" placeholder="分数" class="bc_formiteminput" size="small" @keyup.enter.native=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="bc_formitem">
                        <el-form-item>
                            <el-button type="primary" @click="save()">保 存</el-button>
                            <el-button type="primary" @click="bc_edit_visible = false">取 消</el-button>
                        </el-form-item>
                    </div>
                </el-form>
            </modal>
    
        </div>
        <div class="bc_fourth">
            <el-pagination :current-page.sync="currentpage" layout="prev, pager, next, jumper" :page-count="pagecount" @current-change="display()">
            </el-pagination>
        </div>
    </div>
</template>

<script>

import Vue from 'vue'
import modal from '../components/modal.vue'
import * as api from '../api'

export default {
    name: 'blockchain',
    components: {
        modal,
    },
    data() {
        return {
            //各个表格的数据
            bc_table_1: { is_blue: true, data: [], hash: '' },
            bc_table_2: { is_blue: true, data: [], hash: '' },
            bc_table_3: { is_blue: true, data: [], hash: '' },
            bc_table_4: { is_blue: true, data: [], hash: '' },
            bc_table_5: { is_blue: true, data: [], hash: '' },
            //活动的tab
            active_tab: 'ip1',
            //当前页码和总页数
            currentpage: 1,
            pagecount: 1,
            bc_edit_visible: false,
            bc_edit_form: {},
            //字段名称在这里统一定义
            bc_column: { order_id: "id", money: "num" },
            //点击编辑后，告诉编辑的是哪个表哪一行
            bc_editindex: '',
            bc_edittable: [],
        }
    },
    created() {
        this.display()
    },
    methods: {
        //编辑此行信息
        edit(index, bc_table) {
            this.bc_edit_visible = true
            this.bc_edit_form[this.bc_column.order_id] = bc_table[index][this.bc_column.order_id]
            this.bc_edit_form[this.bc_column.money] = bc_table[index][this.bc_column.money]
            this.bc_editindex = index
            this.bc_edittable = bc_table
        },

        //保存此行修改
        save() {
            this.bc_edittable[this.bc_editindex][this.bc_column.order_id] = this.bc_edit_form[this.bc_column.order_id]
            this.bc_edittable[this.bc_editindex][this.bc_column.money] = this.bc_edit_form[this.bc_column.money]
            let submitdata = {}
            submitdata.ip = this.active_tab
            submitdata.data = [{id: this.bc_edit_form[this.bc_column.order_id], num: this.bc_edit_form[this.bc_column.money]}]
            api.update((rs)=>{
            }, submitdata)
            this.$message({
                showClose: true,
                type: 'success',
                message: '保存成功'
            })
            this.bc_edit_visible = false
        },
        //展现页面
        display() {
            let submitdata = [{ ip: this.active_tab, page: this.currentpage }]
            api.get_blockchain_detail((rs) => {
                let bc_table_list = [this.bc_table_1, this.bc_table_2, this.bc_table_3, this.bc_table_4, this.bc_table_5]
                for (let i = 0; i < 5; i++) {
                    bc_table_list[i].data = []
                    bc_table_list[i].hash = ''
                    bc_table_list[i].is_yellow = false
                    bc_table_list[i].is_blue = true
                    bc_table_list[i].is_red = false
                }
                for (let i = 0; i < rs.block_data.length; i++) {
                    bc_table_list[i].data = rs.block_data[i].data
                    bc_table_list[i].hash = rs.block_data[i].hash
                    if (rs.block_data[i].state == 0) {
                        bc_table_list[i].is_yellow = false
                        bc_table_list[i].is_blue = false
                        bc_table_list[i].is_red = true
                    }
                    if (rs.block_data[i].state == 1) {
                        bc_table_list[i].is_yellow = false
                        bc_table_list[i].is_blue = true
                        bc_table_list[i].is_red = false
                    }
                    if (rs.block_data[i].state == 2) {
                        bc_table_list[i].is_yellow = false
                        bc_table_list[i].is_blue = false
                        bc_table_list[i].is_red = true
                    }


                }
                this.pagecount = rs.total_page
            }, submitdata)

        },
        tab_click() {
            this.display()
        }
    }
}

</script>

<style>
#blockchain {
    width: 100%;
    height: 100%;
    font-family: "Microsoft YaHei";
    background: url("../assets/a.png");
}

.bc_table {
    font-size: 10px;
    text-align: center;
    width: 10%;
}

.bc_first {
    width: 90%;
    margin: 10px auto;
    min-width: 1000px;
}

.bc_second {
    width: 90%;
    margin: 10px auto;
    min-width: 1000px;
    height: 30px;
}

.bc_third {
    width: 90%;
    margin: 10px auto;
    min-width: 1000px;
}

.bc_fourth {
    width: 90%;
    margin: 30px auto;
    min-width: 1000px;
    height: 40px;
}

.bc_first1 {
    width: 19%;
    float: left;
    border: 1px solid #4D4D4D;
}

.lianzi {
    width: 65px;
    height: 37px;
    float: left;
}

.block {
    float: left;
    height: 20px;
    width: 2%;
    border-radius: 5px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
}

.block_red {
    background-color: red;
}

.block_blue {
    background-color: #232277;
}

.block_yellow {
    background-color: yellow;
}

.line {
    border: 1px solid #232277;
    margin-top: 10px;
}

.noline {
    margin-top: 10px;
}

.line_div {
    float: left;
    height: 20px;
    width: 8.5%;
}

.line_connect {
    float: left;
    height: 20px;
    width: 1%;
}

.hash {
    width: 20%;
    font-size: 10px;
    height: 80px;
    word-break: break-all;
    float: left;
    padding-left: 50px;
    padding-right: 50px;
}

.bc_first2 {
    width: 1%;
    height: 1px;
    float: left;
}

.bc_formitem {
    width: 350px;
    margin: 10px auto;
}

.bc_formiteminput {
    width: 180px;
}

.el-icon-circle-cross:hover {
    cursor: pointer;
}
</style>


