<template>
    <div id="handle">
        <div class="handle_first">
            <div class="mingcheng1">流水单号:</div>
            <el-input style="width:200px; float:left; margin-left:10px;" size="small" v-model="orderid_input" placeholder="流水单号"></el-input>
            <div class="mingcheng2">分数:</div>
            <el-input style="width:200px; float:left; margin-left:10px;" size="small" v-model="money_input" placeholder="分数"></el-input>
            <div style="float:left;margin-left:10px;">
                <el-button style="margin-right:20px;float:right;height:30px;padding:0px;width:70px;font-family: 'Microsoft YaHei';" type="primary" @click="auto_add()">自动新增</el-button>
                <el-button style="margin-right:20px;float:right;height:30px;padding:0px;width:70px;font-family: 'Microsoft YaHei';" type="primary" @click="add()">新 增</el-button>
    
            </div>
        </div>
        <div class="handle_second">
            <el-table class="handle_table" :data="handle_table" stripe border v-loading="handle_loading" element-loading-text="拼命提交中" style="width: 100%">
                <el-table-column label="序号" header-align="center" width="100">
                    <template scope="scope">
                        <p>{{ scope.$index + 1 }}</p>
                    </template>
                </el-table-column>
                <el-table-column prop="id" label="流水单号" header-align="left" show-overflow-tooltip align="left">
                </el-table-column>
                <el-table-column prop="num" label="分数" header-align="left" show-overflow-tooltip align="left">
                </el-table-column>
                <el-table-column label="操作" width="100">
                    <template scope="scope">
                        <el-button @click.native.prevent="edit(scope.$index)" size="small" type="text">
                            编辑
                        </el-button>
                        <el-button @click.native.prevent="deleterow(scope.$index)" size="small" type="text">
                            移除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <modal v-if="handle_edit_visible">
                <template slot="header">编辑
                    <div style="float:right"><i @click="handle_edit_visible=false" class="el-icon-circle-cross"></i></div>
                </template>
                <el-form :inline="true" :model="edit_form" label-position="left" ref="ruleForm" label-width="70px">
                    <div class="handle_formitem">
                        <el-form-item label="流水单号" prop="id">
                            <el-input v-model="edit_form.id" placeholder="流水单号" class="handle_formiteminput" size="small"></el-input>
                        </el-form-item>
                    </div>
                    <div class="handle_formitem">
                        <el-form-item label="分数" prop="num">
                            <el-input v-model="edit_form.num" placeholder="分数" class="handle_formiteminput" size="small" @keyup.enter.native=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="handle_formitem">
                        <el-form-item>
                            <el-button type="primary" @click="save()">保 存</el-button>
                            <el-button type="primary" @click="handle_edit_visible = false">取 消</el-button>
                        </el-form-item>
                    </div>
                </el-form>
            </modal>
        </div>
        <div class="handle_third">
            <el-button type="primary" style="float:right;width:70px;height:30px;padding:0px;font-family: 'Microsoft YaHei';" @click="submit()">提 交</el-button>
        </div>
    </div>
</template>

<script>
import Vue from 'vue'
import modal from '../components/modal.vue'
import axios from 'axios'
import * as api from '../api'

export default {
    name: 'handle',
    components: {
        modal,
    },
    data() {
        return {
            orderid_input: '',
            money_input: '',
            //字段名称在这里统一定义
            handle_column: { order_id: "id", money: "num" },
            handle_table: [],
            edit_form: {},
            handle_loading: false,
            //当前编辑第几行
            editindex: 0,
            handle_edit_visible: false,

        }
    },
    methods: {
        submit() {
            let submitdata = this.handle_table
            window.localStorage.setItem("last_order_id", this.handle_table[this.handle_table.length - 1][this.handle_column.order_id])
            api.submit_data((rs) => {
                this.$message({
                    showClose: true,
                    type: 'success',
                    message: '提交成功'
                })
                this.handle_table.splice(0, this.handle_table.length)
            }, submitdata)
        },
        add() {
            //增加的信息
            var add_info = new Object()
            add_info[this.handle_column.order_id] = this.orderid_input
            add_info[this.handle_column.money] = this.money_input

            this.handle_table.splice(this.handle_table.length, 0, add_info)

            this.$message({
                showClose: true,
                type: 'success',
                message: '添加成功'
            })
            //重置输入框
            this.orderid_input = ''
            this.money_input = ''

        },
        //自动新增10行数据
        auto_add() {

            //时间戳作为随机的order_id
            let order_id = 1
            if (window.localStorage.getItem("last_order_id")) {
                order_id = parseInt(window.localStorage.getItem("last_order_id")) + 1
            } else {
                order_id = 1
            }
            
            for (let i = 0; i < 10; i++) {
                var add_info = new Object()
                let money = Math.floor(Math.random() * 10 + 1)
                let fuhao = Math.floor(Math.random() * 2)
                add_info[this.handle_column.order_id] = order_id
                if (fuhao == 0) {
                    add_info[this.handle_column.money] = money
                } else {
                    add_info[this.handle_column.money] = -money
                }

                order_id++
                console.log(order_id)
                //添加到数据中
                this.handle_table.splice(this.handle_table.length, 0, add_info)
            }


            this.$message({
                showClose: true,
                type: 'success',
                message: '添加成功'
            })
        },
        //编辑此行信息
        edit(index) {
            this.handle_edit_visible = true
            this.edit_form[this.handle_column.order_id] = this.handle_table[index][this.handle_column.order_id]
            this.edit_form[this.handle_column.money] = this.handle_table[index][this.handle_column.money]
            this.editindex = index
        },
        //保存此行修改
        save() {
            this.handle_table[this.editindex][this.handle_column.order_id] = this.edit_form[this.handle_column.order_id]
            this.handle_table[this.editindex][this.handle_column.money] = this.edit_form[this.handle_column.money]
            this.$message({
                showClose: true,
                type: 'success',
                message: '保存成功'
            })
            this.handle_edit_visible = false
        },
        //删除此行
        deleterow(index) {
            this.$confirm('此操作将删除此行，是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.handle_table.splice(index, 1)
                this.$message({
                    showClose: true,
                    type: 'success',
                    message: '已成功删除'
                })
            }).catch(() => {
                this.$message({
                    showClose: true,
                    type: 'info',
                    message: '已取消删除'
                })
            })
        },
    }
}

</script>

<style>
#handle {
    width: 100%;
    height: 100%;
    font-family: "Microsoft YaHei";
    background: url("../assets/a.png");
    padding-top: 10px;
}

.handle_first {
    width: 70%;
    height: 30px;
    margin: 0px auto;
    font-size: 14px;
    min-width: 1000px;
}

.handle_first1 {
    width: 300px;
    height: 40px;
    float: left;
    text-align: left;
}


.mingcheng1 {
    float: left;
    width: 60px;
    height: 30px;
    line-height: 30px;
}

.mingcheng2 {
    float: left;
    width: 40px;
    height: 30px;
    line-height: 30px;
    margin-left: 20px;
}

.handle_input {
    float: left;
    width: 100px;
    margin-left: 5px;
}

.handle_table {
    font-size: 10px;
    text-align: center;
}

.handle_second {
    width: 70%;
    margin: 10px auto;
    min-width: 1000px;
}

.handle_third {
    width: 70%;
    margin: 10px auto;
    min-width: 1000px;
}

.handle_formitem {
    width: 350px;
    margin: 10px auto;
}

.handle_formiteminput {
    width: 180px;
}

.el-icon-circle-cross:hover {
    cursor: pointer;
}
</style>


